.secondSection {
    position: relative;
    width: 100%;
    height: 100vh;
    background-color: black;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}

.secondSectionText {
    font-size: 7rem;
    color: rgba(255, 255, 255, 0);
    animation: textFadeIn 3s ease forwards, textMoveUp 1.5s ease forwards;
}

@keyframes textFadeIn {
    0% {
        color: rgba(255, 255, 255, 0);
        text-shadow: 0 0 10px rgba(255, 0, 0, 0.8);
    }
    100% {
        color: rgba(255, 255, 255, 1);
        text-shadow: 0 0 30px rgba(255, 0, 0, 0.8),
        0 0 60px rgba(255, 153, 0, 0.8),
        0 0 90px rgba(255, 255, 0, 0.8);
    }
}

@keyframes textMoveUp {
    0% {
        transform: translateY(100vh); /* 화면 아래에서 시작 */
    }
    100% {
        transform: translateY(0); /* 중앙에 멈춤 */
    }
}